*{
    background-color: #2b2a28;
}

.App{
   display: flex;
    flex-direction: column;
    align-items: center;
}
.video-player{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 20px;
}
.button{
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 25px;
}

.button:hover{
    background-color: #45a049;
}

.text{
    color: white;
    font-size: 20px;
    margin-top: 20px;
}
